一份报纸，或是和恋人、朋友及家人在一起共享温馨舒适、乐趣无穷的咖啡时光，是一种幸福。")])]),_c('div',{staticClass:"ig-tp"},[_c('img',{attrs:{"src":require("../assets/tect4.jpg"),"alt":""}})])])}]

